import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  TextInput,
  ScrollView,
  Alert,
  Platform,
} from "react-native";
import AsyncStorage from "@react-native-async-storage/async-storage";
import Checkbox from "expo-checkbox";
import { StatusBar } from "expo-status-bar";
import { theme } from "./colors";
import { useEffect, useState } from "react";
import { Fontisto } from "@expo/vector-icons";

const TODO_STORAGE_KEY = "@toDos";
const WORKING_STORAGE_KEY = "@working";

export default function App() {
  const [working, setWorking] = useState(true);
  const [text, setText] = useState("");
  const [toDos, setToDos] = useState({});
  const [editingTodo, setEditingTodo] = useState(null);
  const [editMessage, setEditMessage] = useState("");
  const travel = () => setWorking(false);
  const work = () => setWorking(true);
  const onChangeText = (payload) => setText(payload);

  const saveTodos = async (toSave) => {
    try {
      await AsyncStorage.setItem(TODO_STORAGE_KEY, JSON.stringify(toSave));
    } catch (e) {}
  };

  const addTodo = async () => {
    if (text === "") {
      return;
    }
    const newTodos = {
      ...toDos,
      [Date.now()]: { text, working, completed: false },
    };
    setToDos(newTodos);
    await saveTodos(newTodos);
    setText("");
  };

  const loadTodos = async () => {
    try {
      const s = await AsyncStorage.getItem(TODO_STORAGE_KEY);
      if (s) {
        setToDos(JSON.parse(s));
      }
    } catch (e) {
      console.log(e);
    }
  };

  const deleteTodo = (key) => {
    if (Platform.OS === "web") {
      const ok = confirm("Do you want to delete to do?");
      if (ok) {
        const newTodos = { ...toDos };
        delete newTodos[key];
        setToDos(newTodos);
        saveTodos(newTodos);
      }
    } else {
      Alert.alert("Delete To Do", "Are you sure?", [
        { text: "Cancel" },
        {
          text: "I'm Sure",
          onPress: async () => {
            const newTodos = { ...toDos };
            delete newTodos[key];
            setToDos(newTodos);
            await saveTodos(newTodos);
          },
        },
      ]);
    }
  };
  const saveWorking = async (toSave) => {
    try {
      await AsyncStorage.setItem(WORKING_STORAGE_KEY, JSON.stringify(toSave));
    } catch (e) {}
  };
  const loadWorking = async () => {
    try {
      const s = await AsyncStorage.getItem(WORKING_STORAGE_KEY);
      if (s) {
        setWorking(s === "true");
      }
    } catch (e) {
      console.log(e);
    }
  };
  const setChecked = async (key) => {
    const newTodos = {
      ...toDos,
    };
    newTodos[key].completed = !newTodos[key].completed;
    setToDos(newTodos);
    await saveTodos(newTodos);
  };

  const editTodo = (key, value) => {
    if (editingTodo != null) return;
    setEditingTodo(key);
    setEditMessage(value);
  };
  const updateTodo = async (key) => {
    const newTodos = {
      ...toDos,
    };
    newTodos[key].text = editMessage;
    setEditingTodo(null);
    setEditMessage("");
    setToDos(newTodos);
    await saveTodos(newTodos);
  };

  useEffect(() => {
    loadTodos();
    loadWorking();
  }, []);

  useEffect(() => {
    saveWorking(working);
  }, [working]);

  return (
    <View style={styles.container}>
      <StatusBar style="light"></StatusBar>
      <View style={styles.header}>
        <TouchableOpacity onPress={work}>
          <Text
            style={{
              fontWeight: 600,
              fontSize: 38,
              color: working ? "white" : theme.grey,
            }}
          >
            Work
          </Text>
        </TouchableOpacity>
        <TouchableOpacity onPress={travel}>
          <Text
            style={{
              fontWeight: 600,
              fontSize: 38,
              color: !working ? "white" : theme.grey,
            }}
          >
            Travel
          </Text>
        </TouchableOpacity>
      </View>
      <View>
        <TextInput
          value={text}
          onChangeText={onChangeText}
          onSubmitEditing={addTodo}
          returnKeyType="done"
          placeholder={working ? "Add a To Do" : "Where do you want to go?"}
          style={styles.input}
        />
      </View>
      <ScrollView>
        {Object.keys(toDos).map((key) =>
          toDos[key].working === working ? (
            <View key={key} style={styles.toDo}>
              <View style={{ flexDirection: "row", alignItems: "center" }}>
                <Checkbox
                  style={styles.checkbox}
                  value={toDos[key].completed}
                  onValueChange={() => setChecked(key)}
                  color={"#0895E9"}
                />
                {key === editingTodo ? (
                  <TextInput
                    autoFocus
                    value={editMessage}
                    onChangeText={(value) => setEditMessage(value)}
                    onSubmitEditing={() => updateTodo(key)}
                    returnKeyType="done"
                    placeholder={
                      working ? "Add a To Do" : "Where do you want to go?"
                    }
                    style={styles.editInput}
                  />
                ) : (
                  <Text
                    style={{
                      color: "white",
                      fontSize: 16,
                      fontWeight: 500,
                      textDecorationLine: toDos[key].completed
                        ? "line-through"
                        : "none",
                    }}
                  >
                    {toDos[key].text}
                  </Text>
                )}
              </View>
              {key === editingTodo && editingTodo ? null : (
                <View style={{ flexDirection: "row" }}>
                  <TouchableOpacity
                    onPress={() => editTodo(key, toDos[key].text)}
                    style={{ marginRight: 20 }}
                  >
                    <Fontisto name="eraser" size={16} color={theme.grey} />
                  </TouchableOpacity>
                  <TouchableOpacity onPress={() => deleteTodo(key)}>
                    <Fontisto name="trash" size={16} color={theme.grey} />
                  </TouchableOpacity>
                </View>
              )}
            </View>
          ) : null
        )}
      </ScrollView>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,

    backgroundColor: theme.bg,
    paddingHorizontal: 20,
  },
  header: {
    flexDirection: "row",
    justifyContent: "space-between",
    marginTop: 100,
  },
  btnText: {
    fontWeight: 600,
    fontSize: 38,
  },
  input: {
    backgroundColor: "white",
    paddingVertical: 15,
    paddingHorizontal: 20,
    borderRadius: 30,
    marginVertical: 20,
    fontSize: 18,
  },
  toDo: {
    backgroundColor: theme.toDoBg,
    marginBottom: 10,
    paddingHorizontal: 20,
    paddingVertical: 20,
    borderRadius: 15,
    flexDirection: "row",
    justifyContent: "space-between",
  },
  toDoText: {
    color: "white",
    fontSize: 16,
    fontWeight: 500,
  },
  checkbox: { marginRight: 10 },
  editInput: {
    backgroundColor: "white",
    paddingVertical: 5,
    paddingHorizontal: 10,
    borderRadius: 10,
    fontSize: 16,
    width: "90%",
  },
});
